const email = () => {
  const d = document;


  d.addEventListener('submit', e => {
    if (e.target.matches('#contact-form')) {
      e.preventDefault();

      const btn = d.querySelector('#enviar'),
        name = d.querySelector('input[name="name"]').value,
        email = d.querySelector('input[name="email"]').value,
        subject = d.querySelector('input[name="subject"]').value,
        mesagge = d.querySelector('textarea[name="mesagge"]').value;

      btn.textContent = 'Enviando mensaje';

      Email.send({
        Host: "smtp.gmail.com",
        Username: "pablorincones95@gmail.com",
        Password: "v--24418291",
        To: 'pablorincones95@gmail.com',
        From: email,
        Subject: subject,
        Body: mesagge
      }).then(
        message => {
          btn.textContent = 'Enviar mensaje';
          d.getElementById('contact-form').reset();
         if (message === 'OK') {
            e.target.querySelector('.u-message').innerHTML = 'Correo enviado <br> <i class="far fa-smile-wink"></i>';
          } else {
            e.target.querySelector('.u-message').innerHTML = ' El correo no se a enviado <br> <i class="far fa-sad-cry"></i>';
          }

        }
      );
    }
  })


}

export default email;
